import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AddPacksizeComponent } from './add-packsize/add-packsize.component';
import { EditPacksizeComponent } from './edit-packsize/edit-packsize.component';
import { PacksizeManagementComponent } from './packsize-management.component';
import { RouterModule, Routes } from '@angular/router';

const routes: Routes = [ 
  {
    path: '',
    data: {
      title: 'Packsize Management'
    },
    children: [
      {
        path: '',
        component: PacksizeManagementComponent,
        data: {
          title: 'List'
        }
      },
      {
        path: 'add',
        component: AddPacksizeComponent,
        data: {
          title: 'Add'
        },
      },
      {
        path: 'edit',
        component: EditPacksizeComponent,
        data: {
          title: 'Edit'
        },
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})

export class PacksizeManagementRoutingModule { }
