import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';
import { PacksizeManagementService } from '../../../shared/services/packsize-management.service';
import { Validations } from '../../../shared/validators/Validations';

@Component({
  selector: 'app-edit-packsize',
  templateUrl: './edit-packsize.component.html'
})
export class EditPacksizeComponent implements OnInit {
  
  editForm: FormGroup;
  submitted: boolean = false;
  packsizeId : any;

  constructor(
  	private titleService: Title,
    private packsizeManagementService: PacksizeManagementService,
    private toastr: ToastrService,
    private router:Router, 
    private route:ActivatedRoute
  ) { }

  ngOnInit(): void {
  	this.titleService.setTitle("Edit Packsize");

    this.editForm = new FormGroup({
      size: new FormControl('', [
        Validators.required,
        Validators.pattern(Validations.whitespace),
        Validators.minLength(2)
      ])
    });

    this.getPacksizeInfo();
  }

  getPacksizeInfo(){
  	this.route.queryParams.subscribe(param => {
      this.packsizeId = param.id;
    });
    
    this.packsizeManagementService.getPacksize(this.packsizeId).subscribe(response => {
        let resData = JSON.parse(JSON.stringify(response));

        if(resData.status_code == 200){
          this.editForm.patchValue({
          	size: resData.data.size
          });
        }else{
          this.toastr.error('', resData.message);
        }
      },
        err => {
          this.toastr.error('', err.error.message);
        });
  }

  submitEditForm(){
    this.submitted = true;

      var formData = new FormData();
      formData.append("size", this.editForm.controls.size.value);

      if (this.editForm.valid) {
      this.packsizeManagementService.updatePacksize(this.packsizeId,formData).subscribe(response => {
        let resData = JSON.parse(JSON.stringify(response));

        if(resData.status_code == 200){
          this.router.navigate(['packsize-management']).then(() => {
            this.toastr.success('', "Packsize updated successfully!");
          });
        }else{
          this.toastr.error('', resData.message);
        }
      },
        err => {
          this.toastr.error('', err.error.message);
        });

    }
  }

}
