import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';

@Injectable()
export class PacksizeManagementService {

  constructor(private http: HttpClient) { }

  packsizelist(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/pack/listview/`, data);
  }

  getPacksize(id) : Observable<any>{
    return this.http.get<any>(`${environment.base_url}/packsizes/${id}/`);
  }

  createPacksize(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/pack/`, data);
  }

  updatePacksize(id,data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/pack/${id}/edit/`, data);
  }

  deletePacksize(id): Observable<any> {
    return this.http.delete<any>(`${environment.base_url}/pack/${id}/`);
  }

}
