import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';
import { PacksizeManagementService } from '../../../shared/services/packsize-management.service';
import { Validations } from '../../../shared/validators/Validations';

@Component({
  selector: 'app-add-packsize',
  templateUrl: './add-packsize.component.html'
})
export class AddPacksizeComponent implements OnInit {
  
  addForm: FormGroup;
  submitted: boolean = false;

  constructor(
  	private titleService: Title,
    private packsizeManagementService: PacksizeManagementService,
    private toastr: ToastrService,
    private router:Router, 
    private route:ActivatedRoute
  ) { }

  ngOnInit(): void {
  	this.titleService.setTitle("Add Packsize");

    this.addForm = new FormGroup({
      size: new FormControl('', [
        Validators.required,
        Validators.pattern(Validations.whitespace),
        Validators.minLength(2)
      ])
    });
  }

  submitAddForm(){
    this.submitted = true;

      var formData = new FormData();
      formData.append("size", this.addForm.controls.size.value);

      if (this.addForm.valid) {
      this.packsizeManagementService.createPacksize(formData).subscribe(response => {
        let resData = JSON.parse(JSON.stringify(response));

        if(resData.status_code == 200){
          this.router.navigate(['packsize-management']).then(() => {
            this.toastr.success('', "Packsize added successfully!");
          });
        }else{
          this.toastr.error('', resData.message);
        }
      },
        err => {
          this.toastr.error('', err.error.message);
        });

    }
  }


}
