import { Component, OnInit,TemplateRef, ViewChild, AfterViewInit, OnDestroy } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { DataTableDirective } from 'angular-datatables';
import { ToastrService } from 'ngx-toastr';
import { PacksizeManagementService } from '../../shared/services/packsize-management.service';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-packsize-management',
  templateUrl: './packsize-management.component.html'
})
export class PacksizeManagementComponent implements AfterViewInit, OnDestroy, OnInit {

  @ViewChild('deleteModal') public deleteModal: ModalDirective;
  @ViewChild(DataTableDirective)
  dtElement: DataTableDirective;
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();
  packsizeList: Array<any>;
  packsizeId:any;

  constructor(
  	private titleService: Title,
    private toastr: ToastrService,
    private packsizeManagementService: PacksizeManagementService
  ) { }

  ngOnInit(): void {
  	this.titleService.setTitle("Packsize List");
    this.getPacksizeList();
  }

  getPacksizeList(){
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 100,
      //order: [1, 'desc'],
      serverSide: true,
      processing: true,
      searchDelay: 1300,
      ajax: (dataTablesParameters: any, callback) => {
        dataTablesParameters.page = parseInt(dataTablesParameters.start) / parseInt(dataTablesParameters.length) + 1;
        dataTablesParameters.limit = dataTablesParameters.length;
        dataTablesParameters.search_param = dataTablesParameters.search.value;

        // sorting
        dataTablesParameters.order[0].column == 0 ? dataTablesParameters.size = (dataTablesParameters.order[0].column == 0 && dataTablesParameters.order[0].dir == "asc") ? ("size") : ("-size") : "";

        this.packsizeManagementService
          .packsizelist(dataTablesParameters)
          .subscribe(
            respones => {
              let resData = JSON.parse(JSON.stringify(respones));
              if(resData.status_code == 200){
                this.packsizeList = resData.data;

                callback({
                  recordsTotal: resData.total_record,
                  recordsFiltered: dataTablesParameters.search_param == "" ? resData.total_record : resData.filter_record,
                  data: []
                });
              }else{
                this.toastr.error('', resData.message);
              }

            },
            err => {
              this.toastr.error('', err.error.message);
            }
          );
      },
      columns: [
        { data: 'size' },
        { data: 'action', searchable: false, orderable: false }]
    };
  }

  ngAfterViewInit(): void {
    this.dtTrigger.next();
  }

  ngOnDestroy(): void {
    this.dtTrigger.unsubscribe();
  }

  saveId(packsizeId){
    this.packsizeId = packsizeId;
  }

  deletePacksize() {
          this.packsizeManagementService
            .deletePacksize(this.packsizeId)
            .subscribe(
              response => {
                let resData = JSON.parse(JSON.stringify(response));
                if (resData.status_code == 200) {
                  this.deleteModal.hide();
                  this.toastr.success('', "Packsize deleted successfully.")
                  this.rerender();
                } else {
                  this.toastr.error('', resData.message);
                }
              },
              err => {
                this.toastr.error('',err.error.mesaage);
              }
            );
  }

  rerender(): void {
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
      dtInstance.destroy();
      this.dtTrigger.next();
    });
  }

}
